import { useWeb3React } from "@web3-react/core"
import React, { useEffect, useState } from "react"
import styled from "styled-components"
import { ButtonPrimary, PendingContent } from "../Button"
import { AirdropService } from "../../services/AirdropService";
import { supportedChain } from "../../utils";
import { ROOTED_TICKER } from "../../constants";

const ClaimButton = styled(ButtonPrimary)`
    width: 100%;
    text-transform: none;
    white-space: nowrap;
    padding: 0.55em;
    min-width: 8em;
`

export const Airdrop = () => {
    const { account, library, chainId } = useWeb3React();
    const [claming, setClaming] = useState<boolean>(false);
    const [amount, setAmount] = useState<string>("");
    const [claimed, setClaimed] = useState<boolean>(false);

    useEffect(() => {

        const getAmount = async () => {
            const service = new AirdropService(library, account!);
            const amountToClaim = await service.getAmout();
            setAmount(amountToClaim);
            setClaimed(parseFloat(amountToClaim) === 0);
        }

        if (account && library && supportedChain(chainId)) {
            getAmount();
        }
    },[account, library, chainId])

    const claim = async () => {
        if (account && library && supportedChain(chainId)) {
            try {
                const service = new AirdropService(library, account!);
                setClaming(true);
               
                const txResponse = await service.claim();
                if (txResponse) {
                    await txResponse.wait();
                    setClaimed(true);
                }
                setClaming(false);                
            }
            catch(e){
                console.log(e);
                setClaming(false);
            }            
        }
    }

    return (
    
    <ClaimButton disabled={claming || claimed || !supportedChain(chainId)} onClick={claim}>
        {claming ? <PendingContent text={"Claiming..."}/> : claimed ? `Nothing to claim` : `Claim ${amount} ${ROOTED_TICKER}`}
    </ClaimButton>)
}