import React from 'react';
import styled from 'styled-components';
import { TransactionResponse } from "@ethersproject/providers";
import Web3ReactManager from './components/Web3ReactManager';
//import { MarketGenerationEvent } from './pages/MarketGenerationEvent';
import { Switch, Route, HashRouter } from 'react-router-dom';
import { useToast } from './components/ToastProvider';
//import { Footer } from './components/Footer';
import { IconType } from './components/Icon/IconType';
import TransactionContext from './contexts/TransactionContext';
//import { Overview } from './pages/Overview';
//import { ComingSoon } from './pages/ComingSoon';
import Header from './components/Header';
//import { ClaimRewards } from './pages/ClaimRewards';
import { Swap } from './pages/Swap';
import { Chart } from './pages/Chart';
import Navigation from './components/Navigation';
import { Stake } from './pages/Stake';
import { Footer } from './components/Footer';

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
`
const HeaderWrapper = styled.div`
  ${({ theme }) => theme.flexRowNoWrap}
  width: 100%;
  justify-content: space-between;
`
const BodyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1;
  min-height: calc(100vh - 11.5em);

  ${({ theme }) => theme.mediaWidth.upToMedium`
    min-height: calc(100vh - 20em);
  `};
`

export const App = () => {
  const { addToast } = useToast();

  const addPendingTransaction = (description: string, txResponse: TransactionResponse) => {
    txResponse.wait().then(txReceipt => {
      addToast(
        txReceipt.status === 1 ? IconType.Success : IconType.Failure,
        `${description} ${txReceipt.status === 1 ? "Completed" : "Failed"}`,
        txReceipt.transactionHash)
    })
  }  

 

  return (
    <HashRouter>
      <AppWrapper>
        <TransactionContext.Provider value={{ addPendingTransaction }}>
          <HeaderWrapper>
            <Header />
          </HeaderWrapper>
          <BodyWrapper>
            <Navigation/>
            <Web3ReactManager>           
            <Switch> 
                <Route exact strict path="/" component={Swap} />
                <Route exact strict path="/swap" component={Swap} />                
                <Route exact strict path="/stake" component={Stake} />
                <Route exact strict path="/chart" component={Chart} />
                
                {/* <Route exact strict path="/" component={MarketGenerationEvent} />
                <Route exact strict path="/:ref" component={MarketGenerationEvent} /> */}

                {/* <Route exact strict path="/" component={Overview} />
                  <Route exact strict path="/claim" component={Claim} />
                  <Route exact strict path="/lge" component={LiquidityGenerationEvent} /> */}
              </Switch>
             
            </Web3ReactManager>
          </BodyWrapper>
          <Footer />
        </TransactionContext.Provider>
      </AppWrapper>
    </HashRouter>
  );
}

export default App;
