import React from 'react'
import styled from 'styled-components'
import { NavLink } from 'react-router-dom'
import { darken, lighten } from 'polished'
import Row from '../Row'

const Wrapper = styled.div`
    padding-top: 1em;
`

const HeaderLinks = styled(Row)`
  display: grid;
  justify-self: center;  
  width: fit-content;
  padding: 0.15em;
  border-radius: 1em;  
  grid-auto-flow: column;
  grid-gap: 0.5em;
  align-items:center;
  background-color: ${({ theme }) => lighten(0.07, theme.bg1)};
`

const activeClassName = 'ACTIVE'

const StyledNavLink = styled(NavLink).attrs({
  activeClassName,
})`
  ${({ theme }) => theme.flexRowNoWrap}
  align-items: left;
  border-radius: 1em;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  color: ${({ theme }) => theme.text2};
  font-size: 1rem;
  width: fit-content;
  font-weight: 500;
  padding: 0.5em 0.75em 0.55em 0.75em;

  &.${activeClassName} {
    font-weight: 600;
    color: ${({ theme }) => theme.text1};
    background-color: ${({ theme }) => theme.primary1};
  }

  :hover,
  :focus {
    color: ${({ theme }) => darken(0.1, theme.text1)};
  }`

  export default function Navigation() {
      return (
          <Wrapper>
            <HeaderLinks>
                <StyledNavLink id={`swap-nav-link`} to={'/'} isActive={(match, location) => location.pathname === "/" || location.pathname.toLowerCase() === "/swap"}>
                    Swap
                </StyledNavLink>        
                <StyledNavLink id={`stake-nav-link`} to={'/stake'}>
                    Stake
                </StyledNavLink>
                <StyledNavLink id={`chart-nav-link`} to={'/chart'}>
                    Chart
                </StyledNavLink>
            </HeaderLinks>
          </Wrapper>       
      )
  }