import { Contract } from '@ethersproject/contracts'
import airdropAbi from '../constants/abis/airdrop.json'
import { Web3Provider } from '@ethersproject/providers'
import { AIRDROP_ADDRESS } from '../constants'
import BigNumber from 'bignumber.js'
import { getDisplayBalance } from '../utils/formatBalance'

export class AirdropService {
    private contract: Contract;
    private account: string;
    
    constructor(library: Web3Provider, account: string) {
        this.account = account;
        const signer = library.getSigner(account).connectUnchecked()
        this.contract = new Contract(AIRDROP_ADDRESS, airdropAbi, signer);
    }

    public async claim() {
        return await this.contract.claim();
    }

    public async getAmout() {
        const amount = await this.contract.claimableTokens(this.account);
        return getDisplayBalance(new BigNumber(amount.toString()), 2);
    }
}