import React, { useEffect, useState } from "react"
import styled from "styled-components"
import { PrimaryCard } from "../../components/Card"
import {  Option } from "../../components/Button";
import { lighten } from "polished";
import ChartWrapper from 'kaktana-react-lightweight-charts';
// import { ChartService } from "../../services/ChartService";
import {
    useQuery,
    gql
  } from "@apollo/client";

const PageWrapper = styled.div`
    padding-top: 1.5em;

 ${({ theme }) => theme.mediaWidth.upToSmall`
    padding-top:0em;
 `};
`

const CardContent = styled.div`
    display: grid;
    grid-gap: 1.5em;
    padding-bottom: 0.5em;
    width: 26em;

 ${({ theme }) => theme.mediaWidth.upToExtraSmall`
    width: 100%;
    font-size: 0.75em;
 `};
`

const Title = styled.div`
    padding: 0.5em 0;
    font-size: 2em;
    letter-spacing:0.025em;
    text-align: center;
`

const TabsWrapper = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    padding: 0.15em;
    border-radius: 1em;
    grid-gap: 0.5em;
    background-color: ${({ theme }) => lighten(0.07, theme.bg1)};
`

enum Action {
    c1h,
    c1d
}

const PAIR_DAY_DATA = gql`
    query getPairDayData{  
        pairDayDatas(
            where: { 
                pairAddress: "0x50db5be8c0c878e28fe231c647ef41b395463ffb" 
            }
            orderBy: date
            orderDirection: desc
        ) {
            id
            date
            reserve0
            reserve1
            reserveUSD
        }
    }
`;

// const PAIR_HOUR_DATA = gql`
//     query getPairHourData{  
//         pairHourDatas(
//             where: { 
//               pair: "0x50db5be8c0c878e28fe231c647ef41b395463ffb" 
//             }
//             orderBy: hourStartUnix
//             orderDirection: desc
//         ) {
//               id
//               hourStartUnix
//               reserve0
//               reserve1
//               reserveUSD
//         }
//     }
// `;

function calculatePrice(data:any){
    let price = data.reserveUSD == 0 ? (data.reserve0 / data.reserve1) : (data.reserveUSD / data.reserve1);
    return price;
}

// function sortPrice( a:any, b:any ) {
//     if ( a.price < b.price ){
//       return -1;
//     }
//     if ( a.price > b.price ){
//       return 1;
//     }
//     return 0;
// }

function reverseArray(a:any){
    let result = [];
    for(let x=a.length-1;x>=0;x--){
        result.push(a[x]);
    }
    return result;
}

export const Chart = () => {
    const [action, setAction] = useState<Action>(Action.c1d);
    const [state, setState] = useState<any>({
        options: {
            alignLabels: true,
            timeScale: {
                rightOffset: 0,
                barSpacing: 10,
                fixLeftEdge: false,
                lockVisibleTimeRangeOnResize: false,
                rightBarStaysOnScroll: false,
                borderVisible: false,
                borderColor: "#fff000",
                visible: true,
                timeVisible: true,
                secondsVisible: true,
                downColor: 'rgba(37, 148, 51, 0.2)',
                upColor: 'rgba(191, 55, 48, 0.2)',
            },
            localization: {
            priceFormatter: (price:any) => parseFloat(price).toFixed(6)
            }
        },
        candlestickSeries: [{
            data: []
        }]
    });

    // let { data: hourData } = useQuery(PAIR_HOUR_DATA);
    let { data: dayData } = useQuery(PAIR_DAY_DATA);

    useEffect(() => {
        const getPrice = async () => { 
            // let pairHourDatas = reverseArray(hourData.pairHourDatas);
            if(dayData != null){
                let pairDayDatas = reverseArray(dayData.pairDayDatas);
                        
                if(action == 1){
                    if(dayData){
                        let chartData = [];
                        for(let x=0;x<pairDayDatas.length;x++){
                            let date = pairDayDatas[x].date;
                            let price = calculatePrice(pairDayDatas[x]);
                            let closePrice = (x+1 < pairDayDatas.length) ? calculatePrice(pairDayDatas[x+1]) : price;

                            let data = {
                                time: date,
                                open: price,
                                low: price,
                                high: price,
                                close: closePrice
                            };
                            chartData.push(data);
                        }

                        setState({
                            options: {
                                alignLabels: true,
                                timeScale: {
                                    rightOffset: 0,
                                    barSpacing: 10,
                                    fixLeftEdge: false,
                                    lockVisibleTimeRangeOnResize: false,
                                    rightBarStaysOnScroll: false,
                                    borderVisible: false,
                                    borderColor: "#fff000",
                                    visible: true,
                                    timeVisible: true,
                                    secondsVisible: true
                                },
                                localization: {
                                    priceFormatter: (price:any) => parseFloat(price).toFixed(6)
                                }
                            },
                            candlestickSeries: [{
                                data: chartData
                            }]
                        });
                    }
                }
            }
        };
        const timer = setInterval(() => getPrice(), 1000)
        return () => clearInterval(timer)
    });

    return (
        <PageWrapper>
            <PrimaryCard width="auto">
                <CardContent>
                    <Title>Chart</Title>
                    <TabsWrapper>
                        <Option onClick={() => setAction(Action.c1d)} active={action === Action.c1d}>1d</Option>
                    </TabsWrapper>
                    <ChartWrapper options={state.options} candlestickSeries={state.candlestickSeries} autoWidth height={320} darkTheme={true}/>
                </CardContent>
            </PrimaryCard>
        </PageWrapper>
    )
}